.backdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 450;
  background-color: rgba(0, 0, 0, .5);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.no-scroll {
  overflow: hidden;
}

.modal {
  min-width: 280px;
  min-height: 459px;
  color: #fcfcfc;
  z-index: 500;
  background-color: #fcfcfc;
  padding: 15px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal__button {
  background-color: #fcfcfc;
  border: rgba(0, 0, 0, 0);
  position: absolute;
  top: 12px;
  right: 12px;
}

.modal__icon__close {
  width: 20px;
  height: 20px;
